import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController, AlertController } from 'ionic-angular';
import { FormGroup, FormControl, Validators } from '@angular/forms';

/**
 * Generated class for the SelectBlockNoPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-select-block-no',
  templateUrl: 'select-block-no.html',
})
export class SelectBlockNoPage {
  form;
  blocklist = [

  ];

  ionViewDidLoad() {
    console.log('ionViewDidLoad SelectBlockNoPage');
  }

  constructor(public viewCtrl: ViewController, private alertCtrl: AlertController) {
    this.form = new FormGroup({
      blockName: new FormControl("", Validators.required),
    });

  }

  dismiss(param) {
    this.viewCtrl.dismiss(param ? param : '');
  }

  processForm() {
    console.log(this.form.value.blockName);
    if (this.form.status === 'VALID') {
      this.dismiss(this.form.value.blockName);
    } else {
      this.presentAlert('输入不合法', '请输入楼栋号');
    }
  }

  presentAlert(title: string, subTitle: string) {
    let alert = this.alertCtrl.create({
      title: title,
      subTitle: subTitle,
      buttons: ['确定']
    });
    alert.present();
  }
}



